.container{
    display: flex;
    flex-direction: column;
    margin: auto;
    width: 600px;
    margin-top: 100px;
    background: rgb(238, 222, 174);
    padding-bottom: 30px;
}
.header{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 9px;
    width: 100%;
    margin-top: 30px;
}
.text{
    font-size: 40px;
    font-weight: 700;
    color: rgb(79, 28, 219);
}
.underline{
    width: 61px;
    height: 2px;
    background: rgb(79, 28, 219);
    border-radius: 9px;
}
.inputs{
    margin-top: 55px;
    display: flex;
    flex-direction: column;
    gap: 25px;
}
.input{
    display: flex;
    align-items: center;
    margin: auto;
    width: 480px;
    height: 80px;
    background: whitesmoke;
    border-radius: 6px;
    margin-top: 15px;
}
.input input{
    height: 50px;
    width: 400px;
    color: orange;
    font-size: 19px;
    outline: none;
    border: none;
    background: transparent;
}
.forget-password{
    padding-left: 62px;
    margin-top: 27px;
    color: black;
    font-size: 18px;
}
.forget-password span, .switch-action span{
    color: blue;
    cursor: pointer;
}
.submit-container{
   display: flex;
   gap: 20px;
   margin-left: 200px;
   margin-bottom: 20px;
   margin-top: 40px;
}
.submit{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 220px;
    height: 59px;
    color: whitesmoke;
    background: orange;
    border-radius: 50px;
    font-size: 30px;
    font-weight: 700;
    cursor: pointer;
    border: none;
}
.icon{
    font-size: 30px;
    margin-left: 20px;
    margin-right: 20px;
}
.visible-icon{
    font-size: 30px;
    margin-right: 30px;
}
.switch-action{
    margin-left: 10px;
    margin-top: 20px;
}
